.banner {
	padding: calc(100vw * (190 / 1920)) calc(100vw * (250 / 1920)) calc(100vw * (185 / 1920));
	background: #fafafa url('../img/banner.webp') no-repeat top left;
	background-size: 100% auto;

	@include xl {
		padding: calc(100vw * (185 / $xl)) calc(100vw * (155 / $xl)) calc(100vw * (190 / $xl));
		background-image: url('../img/banner_xl.webp');
	}

	@include lg {
		padding: calc(100vw * (95 / $lg)) calc(100vw * (100 / $lg)) calc(100vw * (93 / $lg));
		background-image: url('../img/banner_lg.webp');
	}

	@include sm {
		padding: calc(100vw * (60 / $sm)) calc(100vw * (57 / $sm)) calc(100vw * (70 / $sm));
		background-image: url('../img/banner_sm.webp');
		background-position: top right;
		background-size: 100% auto;
	}

	@include xs {
		padding: calc(100vw * (35 / $xs-base)) calc(100vw * (48 / $xs-base)) calc(100vw * (25 / $xs-base));
		background-image: url('../img/banner_xs.webp');
		background-position: bottom center;
	}

	&__title {
		font-size: calc(100vw * (60 / 1920));
		line-height: 1.2;
		margin-bottom: calc(100vw * (40 / 1920));

		@include xl {
			font-size: calc(100vw * (60 / $xl));
			margin-bottom: calc(100vw * (40 / $xl));
		}

		@include lg {
			font-size: calc(100vw * (50 / $lg));
			margin-bottom: calc(100vw * (30 / $lg));
		}

		@include sm {
			font-size: calc(100vw * (40 / $sm));
			margin-bottom: calc(100vw * (34 / $sm));
		}

		@include xs {
			font-size: calc(100vw * (25 / $xs-base));
			margin-bottom: calc(100vw * (304 / $xs-base));
			display: flex;
			flex-direction: column;
		}
	}

	&__logo {
		font-family: 'Dyson Sans Modern', sans-serif;
		color: $special;
	}

	/* &__logo {
    width: calc(100vw * (190 / 1920));
    display: inline-block;

    > img {
      width: 100%;
      height: auto;
    }

    @include xl {
      width: calc(100vw * (190 / $xl));
    }

    @include lg {
      width: calc(100vw * (132 / $lg));
    }

    @include sm {
      width: calc(100vw * (120 / $sm));
    }

    @include xs {
      width: calc(100vw * (101 / $xs-base));
    }
  } */

	&__text {
		font-size: calc(100vw * (20 / 1920));
		line-height: 1.2;

		@include xl {
			font-size: calc(100vw * (20 / $xl));
		}

		@include lg {
			font-size: calc(100vw * (20 / $lg));
			width: calc(100vw * (350 / $lg));
		}

		@include sm {
			font-size: calc(100vw * (20 / $sm));
			width: calc(100vw * (290 / $sm));
		}
	}

	&__control {
		margin-top: calc(100vw * (40 / 1920));

		@include xl {
			margin-top: calc(100vw * (40 / $xl));
		}

		@include lg {
			margin-top: calc(100vw * (30 / $lg));
		}

		@include sm {
			margin-top: calc(100vw * (34 / $sm));
		}

		@include xs {
			margin-top: calc(100vw * (304 / $xs-base));
		}
	}

	&__link {
		margin-right: calc(100vw * (40 / 1920));

		@include xs {
			margin-inline: auto;
		}
	}

	&__phone {
		font-size: calc(100vw * (18 / 1920));
		gap: calc(100vw * (15 / 1920));

		> img {
			width: calc(100vw * (24 / 1920));

			@include xl {
				width: calc(100vw * (24 / $xl));
			}

			@include lg {
				width: calc(100vw * (24 / $lg));
			}

			@include sm {
				width: calc(100vw * (22 / $sm));
			}

			@include xs {
				width: calc(100vw * (22 / $xs-base));
			}
		}

		@include xl {
			font-size: calc(100vw * (18 / $xl));
		}

		@include lg {
			font-size: calc(100vw * (15 / $lg));
		}

		@include sm {
			font-size: calc(100vw * (15 / $sm));
		}

		@include xs {
			font-size: calc(100vw * (15 / $xs-base));
		}
	}
}
