.text-serif {
	font-family: sans-serif;
}

.h1 {
	font-size: calc(100vw * (40 / 1920));
	line-height: 1;
	font-weight: 400;
	color: $black;
	margin: 0;
}

.h2 {
	font-size: calc(100vw * (36 / 1920));
	line-height: 1;
	font-weight: 400;
	color: $black;
	text-align: center;
	margin: 0;
}

.h3 {
	font-size: calc(100vw * (30 / 1920));
	line-height: 1;
	font-weight: 400;
	color: $black;
	text-align: center;
	margin: 0;
}

.btn {
	font-size: calc(100vw * (18 / 1920));
	line-height: calc(100vw * (20 / 1920));
	color: $white;
	width: max-content;
	padding: calc(100vw * (14 / 1920)) calc(100vw * (36 / 1920)) calc(100vw * (16 / 1920));
	background-color: $special;
	border: none;
	border-radius: calc(100vw * (50 / 1920));
	display: block;
	position: relative;
	z-index: 1;
	overflow: hidden;

	&::before {
		content: '';
		width: 0;
		height: 0;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		background-color: #730243;
		transition: 0.3s ease;
		z-index: -1;
		border-radius: 50%;
	}

	&:hover {
		color: $white;
	}

	&:hover::before {
		width: 150%;
		height: 500%; /* 150% and 500% because div completely to be fill the space of the button.*/
		transition: 0.5s ease;
	}
}

.btn-check:focus + .btn,
.btn:focus {
	outline: 0;
	box-shadow: none;
}

.prices {
	font-family: sans-serif;
	font-size: calc(100vw * (36 / 1920));
	line-height: 1;
	color: $special;
	gap: calc(100vw * (7 / 1920));
	display: flex;
	align-items: baseline;

	&__old-price {
		font-size: calc(100vw * (24 / 1920));
		text-decoration: line-through;
		color: $light;
	}
}

.sale {
	font-size: calc(100vw * (20 / 1920));
	line-height: 1;
	color: $white;
	padding: calc(100vw * (12 / 1920)) calc(100vw * (7 / 1920));
	background-color: $special-light;
	border-radius: calc(100vw * (14 / 1920));
}

.form-group {
	position: relative;
}

.form-group input.error {
	box-shadow: 0 0 3px 3px #da2525;
}

.form-group label.error {
	font-size: calc(100vw * (15 / 1920));
	color: #da2525;
	width: max-content;
	position: absolute;
	bottom: 105%;
	left: 50%;
	transform: translateX(-50%);
}

@media (min-width: 1920px) {
	.h1 {
		font-size: 40px;
	}

	.h2 {
		font-size: 36px;
	}

	.h3 {
		font-size: 30px;
	}

	.btn {
		font-size: 18px;
		line-height: 20px;
		padding: 14px 36px 16px;
		border-radius: 50px;
	}

	.prices {
		font-size: 36px;
		gap: 8px;

		&__old-price {
			font-size: 24px;
		}
	}

	.sale {
		font-size: 20px;
		padding: 12px 7px;
		border-radius: 14px;
	}

	.form-group label.error {
		font-size: 15px;
	}
}

