  margin: 0;
}

.btn {
  font-size: 0.9375vw;
  line-height: 1.0416666667vw;
  color: #ffffff;
  width: max-content;
  padding: 0.7291666667vw 1.875vw 0.8333333333vw;
  background-color: #bd016f;
  border: none;
  border-radius: 2.6041666667vw;
  display: block;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.btn::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #730243;
  transition: 0.3s ease;
  z-index: -1;
  border-radius: 50%;
}
.btn:hover {
  color: #ffffff;
}
.btn:hover::before {
  width: 150%;
  height: 500%; /* 150% and 500% because div completely to be fill the space of the button.*/
  transition: 0.5s ease;
}

.btn-check:focus + .btn,
.btn:focus {
  outline: 0;
  box-shadow: none;
}

.prices {
  font-family: sans-serif;
  font-size: 1.875vw;
  line-height: 1;
  color: #bd016f;
  gap: 0.3645833333vw;
  display: flex;
  align-items: baseline;
}
.prices__old-price {
  font-size: 1.25vw;
  text-decoration: line-through;
  color: #a7a7a7;
}

.sale {
  font-size: 1.0416666667vw;
  line-height: 1;
  color: #ffffff;
  padding: 0.625vw 0.3645833333vw;
  background-color: #e33490;
  border-radius: 0.7291666667vw;
}

.form-group {
  position: relative;
}

.form-group input.error {
  box-shadow: 0 0 3px 3px #da2525;
}

.form-group label.error {
  font-size: 0.78125vw;
  color: #da2525;
  width: max-content;
  position: absolute;
  bottom: 105%;
  left: 50%;
  transform: translateX(-50%);
}

