.catalog-select {
  position: relative;
}
.catalog-select__caption {
  font-size: 0.78125vw;
  line-height: 2.433;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
  transition: color 0.25s ease;
  display: flex;
  align-items: center;
}
.catalog-select__caption > svg {
  transition: rotate 0.3s ease;
  margin-left: 5px;
}
.catalog-select__caption:not(.collapsed) > svg {
  rotate: -180deg;
}
.catalog-select__caption:hover {
  color: #bd016f;
}
.catalog-select__list-wrapper {
  position: absolute;
  top: 100%;
  left: -1.75em;
  z-index: 1000;
}
.catalog-select__list {
  color: #2b2b2b;
  padding: 1.5em 1.75em;
  background-color: #ffffff;
  box-shadow: 0px 1px 7px 0px rgba(0, 0, 0, 0.15);
}
.catalog-select__item {
  font-size: 1.0416666667vw;
  line-height: 2.5;
  text-align: center;
  min-width: max-content;
  width: 100%;
  border-bottom: 1px solid #e3e3e3;
  position: relative;
}
.catalog-select__item:last-child {
  border: none;
}
.catalog-select__item::after {
  content: "";
  width: 105%;
  height: 0.15625vw;
  background-color: #bd016f;
  position: absolute;
  top: 100%;
  left: 0;
  opacity: 0;
}
.catalog-select__item:hover a, .catalog-select__item.active a {
  color: #bd016f;
  transition: all 0.25s ease;
}
.catalog-select__item:hover::after, .catalog-select__item.active::after {
  opacity: 1;
  transition: opacity 0.25s ease;
}

