.card {
  position: relative;
}
.card__content {
  width: 416px;
  height: 100%;
  padding: 20px 40px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: opacity 0.3s ease;
}
.card__sale {
  position: absolute;
  top: 17px;
  right: -17px;
}
.card__img {
  width: 100%;
  aspect-ratio: 1/1;
  object-fit: contain;
  margin-bottom: 10px;
}
.card__img > img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.card__category {
  font-size: 30px;
  line-height: 1;
  color: #575757;
}
.card__name {
  font-size: 24px;
  line-height: 1.125;
  text-align: left;
  margin-block: 15px 25px;
}
.card__name:hover {
  color: inherit;
}
.card__prices {
  margin-top: auto;
  margin-inline: auto 0;
  position: relative;
  text-align: right;
  display: flex;
  justify-content: end;
  align-items: baseline;
  gap: 8px;
}
.card__buy-btn {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
  transition: opacity 0.1s ease, top 0.3s ease;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.card__buy-btn > .btn {
  width: 200px;
}

@media (min-width: 768px) {
  .card:hover > .card__content {
    opacity: 0.5;
  }
  .card:hover .card__buy-btn {
    opacity: 1;
    top: 40%;
  }
}
@media (max-width: 1536px) {
  .card__content {
    width: 23.3072916667vw;
    padding: 1.3020833333vw 1.953125vw;
